import React, { useEffect, useRef } from "react"

import './styles.scss'
import testImage from '../images/test.png'

const IMAGE_WIDTH = 480
const IMAGE_HEIGHT = 640

const IndexPage = () => {
  const video = useRef()

  useEffect(() => {
    navigator.mediaDevices.getUserMedia({
      video: true,
      audio: false
    })
      .then(stream => {
        video.current.srcObject = stream
      })
      .catch(err => {
        console.error(err)
      })
  }, [])

  return (
    <div style={{
      position: 'absolute',
      top: 0,
      left: 0,
      right: 0,
      bottom: 0,
      backgroundColor: 'black',
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'flex-start'
    }}>
      <div style={{
        padding: 0,
        margin: 0
      }}>
        <video
          ref={ref => video.current = ref}
          style={{
            width: '100%',
            height: '100%'
          }}
          autoPlay
          playsInline
          muted
          poster={testImage}
          width={IMAGE_WIDTH}
          height={IMAGE_HEIGHT}
        />
      </div>
      <div style={{
        flex: 1,
        backgroundColor: 'blue'
      }}>
      </div>
    </div>
  )
}

export default IndexPage
